import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { UserBankinfoListComponent } from './user-bankinfo-list.component';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { UserBankInfoService } from 'src/app/service/user-bank-info.service';

const routes: Routes = [
  {
    path: '',
    component: UserBankinfoListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../user-bankinfo-add/user-bankinfo-add.module').then(m => m.UserBankinfoAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../user-bankinfo-add/user-bankinfo-add.module').then(m => m.UserBankinfoAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    UserBankinfoListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    RouterModule.forChild(routes)
  ],
  providers: [
    UserBankInfoService
  ]
})
export class UserBankinfoListModule { }
