

import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable()
export class UserBankInfoService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getUserBankInfoList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/user-bankinfo`, { params: param });
  }

  addUserBankInfo(data): Observable<any> {
    return this.http.post(`${this.API_URL}/user-bankinfo`, data);
  }

  editUserBankInfo(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/user-bankinfo/${id}`, data);
  }

  deleteUserBankInfo(id): Observable<any> {
    return this.http.delete(`${this.API_URL}/user-bankinfo/${id}`);
  }

  getUserBankInfoById(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/user-bankinfo/${id}`, { params: param });
  }
}